<template>
  <div id="app" class="text-center">
   <h1>Word Translator</h1>
    <h5>Powered By Vue.js</h5>
    <hr>
    <TranslateForm v-on:formSubmit="translateText">
    </TranslateForm>
    <TranslateOutput v-text="translatedText">
    </TranslateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'


export default {
  name: 'app',
  components:{
    TranslateForm,
    TranslateOutput
  },
  data: function () {
    return {
      translatedText:''
    }
  },
  methods: {
    translateText:function(text, language) {
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180103T092448Z.1a49515db0afb424.d53be1c0dd2c2aeea089572d0b322fca53ae37ee&lang='+language+'&text='+text)
      .then((response)=>{
        // console.log(response.body.text[0]);
        this.translatedText = response.body.text[0];
      });
    }
  }
}
</script>

<style>
body {
  background: #fefefe;
}
.col-md-6{
 /* background-color: rgba(0,0,0,0.1);*/
}
#translateForm{
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  justify-content: center;
  text-align: center;
}


</style>
