<template>
  <div id="translateForm" class="row">
    <div class="col-md-6 col-md-offset-3 ">
      <form id="transForm"  v-on:submit="formSubmit" class="well form-inline">
        <input type="text" v-model="textToTranslate" placeholder="Enter a word" class="form-control">
        
        <select v-model="language" class="form-control">
          <option value="ru">Russian</option>
          <option value="es">Spanish</option>
          <option value="fr">French</option>
          <option value="zh">Chinese</option>
          <option value="ar">Arabic</option>
          <option value="el">Greek</option>
          <option value="it">Italian</option>
          <option value="es">Spanish</option>
          <option value="ko">Korean</option>
          <option value="la">Latin</option>
        </select>
        <input type="submit" value="Translate" class="btn btn-primary">
      </form>
    </div>
  </div>
</template>

<script>


export default {
  name: 'translateForm',
  data(){
    return{
      textToTranslate:'',
      language:''
    }
  },
  created(){
    this.language = 'ru';
  },
  methods: {
    formSubmit(e){
      this.$emit('formSubmit', this.textToTranslate, this.language);
      e.preventDefault();
    }
  }
  
}
</script>

<style>
#transForm{
  border-radius: 10px;
  border: 1px solid #ccc; 
  background-color: rgba(0,0,0,0.1);
  padding: 0 40px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;

}
</style>



